import * as React from 'react';

import Container from '../components/gatsby/Container';
import Link from 'gatsby-link';
import Page from '../components/gatsby/Page';

export default () => (
    <Page>
        <Container>
            <h1>Hi from the third page</h1>
            <p>Welcome to TEST PAGE, check out my state</p>
            <ul>
                <li>
                    <Link to="/a-markdown-page/" href="/a-markdown-page/">
                        Show me some Markdown!
                    </Link>
                </li>
                <li>
                    <Link to="/" href="/">
                        Take me back home.
                    </Link>
                </li>
            </ul>
        </Container>
    </Page>
);



